<template>
    <div @click.self="removeCover" class="datail-cover">
        <div class="cover-content">
            <div class="log-text-header">
                <span @click="goToHome">泛见志</span><span class="cover-text-color" @click="goToHome">有内涵不脑残</span>
            </div>
            <div>
                <span class="cover-text" @click="goToHome">首页</span>
                <span class="cover-text" @click="goToHotDoor">贱图</span>
                <span class="cover-text" @click="goToOriginal">贱文</span>
                <span class="cover-text" @click="goToVideo">贱玩</span>
                <span class="cover-text" @click="goToOriginal">段子</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    methods:{
        removeCover(){
            this.$emit("remove-cover");
        },
        goToHome(){
            this.$router.push("/")
            this.$emit("remove-cover")
        },
        goToHotDoor(){
             this.$router.push("/hotdoor")
             this.$emit("remove-cover")
        },
        goToOriginal(){
            this.$router.push("/original")
            this.$emit("remove-cover")
        },
        goToVideo(){
            this.$router.push("/video")
            this.$emit("remove-cover")
        },

    }
}
</script>
<style lang="scss" scoped>
.datail-cover{
    z-index: 101;
    width: 100%;
    height: 100%;
    position:fixed;
    background-color: rgba(0,0,0,0.4);

    .cover-content{
        width: 60%;
        height: 100%;
        background-color: #fff;
        font-style: normal;
        font-variant-ligatures: normal;
        font-variant-caps: normal;
        font-variant-numeric: normal;
        font-variant-east-asian: normal;
        font-weight: normal;
        font-stretch: normal;
        font-size: 0.12rem;
        font-family: 微软雅黑, sans-serif;
        
        .log-text-header{
            padding: 0 15px;
            width: 100%;
            height: 40px;
            border: 1px solid #eaeaea;
