<template>
  <div>
    <div class="container">
      <div class="header-left">
        <img src="../../public/images/log.png" alt="" @click="goToHome" />
      </div>
      <div class="header-right">
        <span class="icon-yonghu" @click="goInLogin(headerImg)" />
        <span class="icon-lishijilu" @click="goToHistory" />
        <span class="icon-liebiao" @click="clickShowCover" />
      </div>
    </div>
    <transition name="move">
      <DetailCover v-show="showDetailCover" @remove-cover="clickHiddenCover" />
    </transition>
  </div>
</template>
<script>
import DetailCover from "./DetailCover.vue";
export default {
  props: ["headerImg"],
  data() {
    return {
      showDetailCover: false,
    };
  },
  methods: {
    clickShowCover() {
      if (!sessionStorage.userInfo) {
        this.$router.push("/login");
      } else {
        this.showDetailCover = true;
      }
    },
    clickHiddenCover() {
      this.showDetailCover = false;
    },
    goInLogin(headerImg) {
      if (!sessionStorage.userInfo) {
        this.$router.push("/login");
      } else {
        this.$router.push({
          name: "Personal",
          query: {
            headerImg: headerImg,
          },
        });
      }
    },
    goToHome() {
      this.$router.go(-1);
    },
    goToHistory() {
      this.$router.push("/history");
    },
    goSerch() {
      alert("此功能未开放，敬请期待！");
    },
  },
  components: {
    DetailCover,
  },
};
</script>
<style lang="scss" scoped>
.move-enter {
  transform: translateX(-100px);
  opacity: 0;
}

.move-enter-active,
.move-leave-active {
  transition: all 0.2s linear;
}

.move-enter-to,
.move-leave {
  transform: translateX(0);
}

.move-leave-to {
  transform: translateX(100px);
  opacity: 0;
}
.container {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 40px;
  background-color: #fb3;

  .header-left {
    img {
      margin: 6px 6px;
      width: 68px;
      height: 28px;
    }
  }

  .header-right {
    position: absolute;
    top: 10px;
    right: 0;
    font-size: 20px;

    span {
      margin: 20px 20px 20px 0px;

      &:last-child {
        margin-right: 10px;
      }
    }
  }
}
</style>