<template>
  <div id="app" @mousewheel.prevent>
    <Header :headerImg="headerImg" />
    <div class="position-model">
      <div>
        <router-link to="/" class="router-style">最新</router-link>
        <router-link to="/hotdoor" class="router-style">热门</router-link>
        <router-link to="/video" class="router-style">视频</router-link>
        <router-link to="/original" class="router-style">原创</router-link>
      </div>
      <BackTop />
      <transition name="animate">
        <router-view
          @chuanzi="cuncu"
          :headerImg="headerImg"
          :history="history"
          @addArrH="recordArrH"
          @removeArrH="fruitsArrH"
          @refreshH="refreshH"
          :pageH="pageH"
          @addArr="recordArr"
          @removeArr="fruitsArr"
          @refresh="refresh"
          :page="page"
          @addArrO="recordArrO"
          @removeArrO="fruitsArrO"
          @refreshO="refreshO"
          :pageO="pageO"
          @addArrV="recordArrV"
          @removeArrV="fruitsArrV"
          @refreshV="refreshV"
          :pageV="pageV"
          class="Router"
        />
      </transition>
    </div>
  </div>
</template>
<script>
import Header from "./components/Header.vue";
import BackTop from "./components/BackTop.vue";
import { request } from "../assets/network/request";
export default {
  data() {
    return {
      headerImg: "",
      history: [],
      story: "",
      pageup: "",
      arrIndex: 1,
      isRouterAlive: true,
      oneArr: [],
      two: [],
      pageH: 1,
      page: 1,
      pageO: 1,
      pageV: 1,
    };
  },
  methods: {
    cuncu() {
      this.story = JSON.parse(localStorage.story);
      this.history.unshift(this.story);
      console.log(this.history);
    },
    recordArrH() {
      this.pageH += 1;
    },
    fruitsArrH() {
      if (this.pageH === 1) {
        console.log("第一页！");
      } else {
        this.pageH -= 1;
      }
    },
    refreshH() {
      this.pageH = Math.floor(Math.random() * 100);
    },

    recordArr() {
      this.page += 1;
    },
    fruitsArr() {
      if (this.page === 1) {
        console.log("第一页！");
      } else {
        this.page -= 1;
      }
    },
    refresh() {
      this.page = Math.floor(Math.random() * 100);
    },

    recordArrO() {
      this.pageO += 1;
    },
    fruitsArrO() {
      if (this.pageO === 1) {
        console.log("第一页！");
      } else {
        this.pageO -= 1;
      }
    },
    refreshO() {
      this.pageO = Math.floor(Math.random() * 100);
    },
    recordArrV() {
      this.pageV += 1;
    },
    fruitsArrV() {
      if (this.pageV === 1) {
        console.log("第一页！");
      } else {
        this.pageV -= 1;
      }
    },
    refreshV() {
      this.pageV = Math.floor(Math.random() * 100);
    },
    saveState(){
      let id= {id: JSON.parse(sessionStorage.userInfo).username}
      if(id){
        localStorage.setItem("history",JSON.stringify(this.history))
      }else{
        this.history.push(id)
        localStorage.setItem("history",JSON.stringify(this.history))
      }
    }
  },
  created() {
    if(JSON.parse(localStorage.history)){
      this.history = JSON.parse(localStorage.history)
    }
    request({
      url: "https://api.uomg.com/api/rand.avatar?format=json",
    })
      .then((res) => {
        this.headerImg = res.data.imgurl;
      })
      .catch((err) => {
        alert(err);
      });
  },
  mounted() {
      window.addEventListener('unload',this.saveState)
  },
  components: {
    Header,
    BackTop,
  },
};
</script>
<style lang="scss" scoped>
#app {
  overflow: hidden;
  
  .router-style {
    display: inline-block;
    margin-top: 40px;
    width: 25%;
    height: 37px;
    text-align: center;
    line-height: 37px;
    text-decoration: none;
    color: #666;
    border-bottom: 1px solid #eaeaea;
  }

  .router-link-exact-active {
    border-bottom: 3px solid #fb3;
  }

  .animate-enter {
  transform: translateX(-100px);
  opacity: 0;
}

.animate-enter-active,
.animate-leave-active {
  transition: all 0.2s linear;
}

.animate-enter-to,
.animate-leave {
  transform: translateX(0);
}

.animate-leave-to {
  transform: translateX(100px);
  opacity: 0;
}
}
</style>
