import Vue from 'vue'
import VueRouter from 'vue-router'
import Newest from '../views/Newest.vue'
/* 
*防止router重复跳转出现报错
*/
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)


const routes = [
    {
        path:"/",
        name:"Newest",
        component:Newest
    },
    {
        path:"/hotdoor",
        name:"HotDoor",
        component:() => import("../views/HotDoor.vue")
    },
    {
        path:"/video",
        name:"Video",
        component:() => import("../views/Video.vue")
    },
    {
        path:"/original",
        name:"Original",
        component:() => import("../views/Original.vue")
    },
    {
        path: '/login',
        name: 'Login',
        component: () => import( '../views/login/Login.vue')
    },
    {
        path: '/personal',
        name: 'Personal',
        component: () => import( '../views/personal/Personal.vue'),
    },
    {
        path:'/personalmodul',
        name:'PersonalModul',
        component:() => import('../views/personal/personalModul/PersonalModul.vue'),
        children:[
            {
                path:'contribution',
                name:'Contribution',
                component:() => import('../views/personal/personalModul/Contribution.vue')
            },
            {
                path:'comment',
                name:'Comment',
                component:() => import('../views/personal/personalModul/Comment.vue')
            },
            {
                path:'follow',
                name:'Follow',
                component:() => import('../views/personal/personalModul/Follow.vue')
            },
            {
                path:'fans',
                name:'Fans',
                component:() => import('../views/personal/personalModul/Fans.vue')
            },
        ]
    },
    {
        path:'/history',
        name:'HiStory',
        component:()=> import('../views/history/HiStory.vue')
    },
    {
        path:'/historydown',
        name:'HiStoryDown',
        component:()=> import('../views/history/HiStoryDown.vue')
    },
    {
        path:'/setuser',
        name:'SetUser',
        component:()=> import('../views/personal/SetUser.vue')  
    },
    {
        path:'/rankwealth',
        name:'RankWealth',
        component:()=> import('../views/personal/RankWealth.vue')  
    },
    {
        path:'/setopinion',
        name:'SetOpinion',
        component:()=> import('../views/personal/SetOpinion.vue')  
    },
    {
        path:'/setnews',
        name:'SetNews',
        component:()=> import('../views/personal/SetNews.vue')  
    },
    {
        path:'/newestdetails',
        name:'NewestDetails',
        component:()=> import('../views/NewestDetails.vue')
    }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
