import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VueScroller from 'vue-scroller'
import WebIM from './utils/WebIM'
import store from './store'
import elementUI from './elementUI.config'
import VueLazyload from 'vue-lazyload'

Vue.use(VueLazyload)

Vue.use(VueScroller)

Vue.use(elementUI)

Vue.prototype.WebIM = WebIM

Vue.config.productionTip = false

Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: '../public/images/bg1.jpg',
  loading: '../public/images/bg1.jpg',
  attempt: 1
})

window.vm = new Vue({
  store,
  router,
  render: h => h(App),
  created() {
    
    let userInfo = sessionStorage.getItem('userInfo')

    if (userInfo) {
      userInfo = JSON.parse(userInfo)
      // 自动登录
      console.log('自动登录')

      store.dispatch('onAutoLoginUser', userInfo)
    }
  
  }
}).$mount('#app')



router.beforeEach((to, from, next) => {
  // 登录验证
  // 先判断用户是否已经登录
  if (store.state.username || to.name === 'Login') {
    next()
  } else if( to.name === 'Newest') {
    next()
  }else{
    if(to.path!='/login'){
      sessionStorage.setItem('referrer',to.path) //储存来源路由
      next({
          path:'/login'
      })
  }
  }
 
})





