import {SET_USERNAME} from './mutation-types'

const { WebIM } = window

export default {
    onRegisterUser(context, { username, password, nickname }) {
        // 进行了es6 Promise封装,就可以根据注册成功,失败执行对应的回调
        return new Promise((resolve, reject) => {

            let options = {
                username,
                password,
                nickname,
                appKey: WebIM.config.appkey,
                success: function (res) {
                    resolve(res)
                },
                error: function (err) {
                    reject(err)
                },
                apiUrl: WebIM.config.apiURL
            };

            WebIM.conn.registerUser(options);

        })

    },
    onLoginUser({commit}, { username, password }) {
       return new Promise((resolve, reject) => {
            // 注意登录推荐使用token登录, 只有可以实现断线重连
            let options = {
                apiUrl: WebIM.config.apiURL,
                user: username,
                pwd: password,
                appKey: WebIM.config.appkey,
                success: function (res) {
                    let token = res.access_token
                    // 登陆成功自动修改当前vuex username为当前登录的用户
                    commit(SET_USERNAME, username)
                    // console.log(token)
                    resolve(token)
                },
                error: function (err) {
                    reject(err)
                }
            };
            WebIM.conn.open(options);
        })
    },
    onAutoLoginUser(context, {username, token}) {
        // token登录没有成功失败的回调
        var options = {
            apiUrl: WebIM.config.apiURL,
            user: username,
            accessToken: token,
            appKey: WebIM.config.appkey
        };
        WebIM.conn.open(options);
    }
}